@import '@/sass/index.scss';












































.product-modal {
	width: 100%;
	display: flex;
}

aside {
	width: 50%;
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;

	img {
		max-width: 100%;
		max-height: 100%;
	}
}

.product-info {
	width: 50%;
	margin: 0;
	padding: $space $space/2;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;

	.details {
		width: 100%;
		height: 90%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
	}

	.price {
		margin: $space/3 0;
	}
}

.specifications {
	text-align: left;
	margin: $space/2 0;
	height: 40%;
	overflow: scroll;

	ul {
		text-align: left;
		list-style: square;
		padding-left: 20px;
		font-size: 16px;
	}
}

